import React from "react";

import Header from "./Header";
import Action from "./Action";
import AddOption from "./AddOption";
import Options from "./Options";
import OptionModal from "./OptionModal";

export default class IndecisionApp extends React.Component {
  state = {
    options: [],
    selectedOption: undefined
  };
  // Legacy code
  // constructor(props) {
  //   super(props);
  //   this.deleteOptions = this.deleteOptions.bind(this);
  //   this.pickOption = this.pickOption.bind(this);
  //   this.addOption = this.addOption.bind(this);
  //   this.deleteOption = this.deleteOption.bind(this);
  //   this.state = {
  //     options: []
  //   };
  // }

  componentDidMount() {
    try {
      const json = localStorage.getItem("options");
      const options = JSON.parse(json);

      if (options) {
        this.setState(() => ({ options: options }));
      }
    } catch (e) {}
  }

  componentDidUpdate(prevProps, prevState) {
    if (prevState.options !== this.state.options) {
      const json = JSON.stringify(this.state.options);
      localStorage.setItem("options", json);
    }
  }

  componentWillUnmount() {
    console.log("Component will unmount");
  }

  deleteOptions = () => {
    this.setState(() => ({ options: [] }));
  };

  deleteOption = option => {
    this.setState(prevState => ({
      options: prevState.options.filter(element => element != option)
    }));
  };

  pickOption = () => {
    const option = this.state.options[
      Math.floor(Math.random() * this.state.options.length)
    ];
    this.setState(() => ({
      selectedOption: option
    }));
  };

  resetPickOption = () => {
    this.setState(() => ({
      selectedOption: undefined
    }));
  };

  addOption = option => {
    if (!option) {
      return "Enter a valid value to add option";
    } else if (this.state.options.indexOf(option) > -1) {
      return "This option already exists";
    }

    this.setState(prevState => ({ options: prevState.options.concat(option) }));
  };

  render() {
    const appSubTitle = "Put your life into the hands of a react app";

    return (
      <div className="indecision-app">
        <Header subtitle={appSubTitle} />
        <div className="container">
          <Action
            hasOptions={this.state.options.length > 0}
            pickOption={this.pickOption}
          />
          <div className="widget">
            <Options
              options={this.state.options}
              deleteOptions={this.deleteOptions}
              deleteOption={this.deleteOption}
            />
            <AddOption addOption={this.addOption} />
          </div>
        </div>
        <OptionModal
          selectedOption={this.state.selectedOption}
          resetPickOption={this.resetPickOption}
        />
      </div>
    );
  }
}
