import React from "react";

import Option from "./Option";

const Options = props => {
  const options = props.options;
  return (
    <div>
      <div className="widget-header">
        <h3>Your Options</h3>
        {props.options.length > 0 && (
          <button onClick={props.deleteOptions} className="btn btn--link">
            Remove All
          </button>
        )}
      </div>
      {props.options.length == 0 && (
        <p className="message">Add an option to your list</p>
      )}
      <ol>
        {options.map((option, index) => {
          return (
            <Option
              key={index}
              deleteOption={props.deleteOption}
              optionText={option}
              count={index + 1}
            />
          );
        })}
      </ol>
    </div>
  );
};

export default Options;
