import React from "react";

export default class AddOption extends React.Component {
  state = {
    error: undefined
  };
  /* 
    Without the experimental feature, allows -> functions
    constructor(props) {
    super(props);
    this.handleAddOption = this.handleAddOption.bind(this);
    this.state = {
      error: undefined
    };
  } 
  
  */

  // arrow function, see more: .babelrc - "plugins": ["transform-class-properties"]
  handleAddOption = e => {
    e.preventDefault();
    const option = e.target.elements.option.value.trim();

    const error = this.props.addOption(option);

    this.setState(() => ({ error: error }));

    if (!error) {
      e.target.elements.option.value = "";
    }
  };
  render() {
    return (
      <div className="add-option">
        {this.state.error && (
          <p className="add-option--error">{this.state.error}</p>
        )}
        <form onSubmit={this.handleAddOption} className="add-option__form">
          <input
            type="text"
            name="option"
            className="add-option__form__input"
          />
          <button className="btn add-option__form__btn">Add Option</button>
        </form>
      </div>
    );
  }
}
