import React from "react";

const Option = props => {
  const option = props.optionText;
  const count = props.count;
  return (
    <li className="option">
      <p className="option__text">
        {count}. {option}
      </p>
      <button
        onClick={() => {
          props.deleteOption(option);
        }}
        className="btn btn--link"
      >
        Remove
      </button>
    </li>
  );
};

export default Option;
